.blog-banner-section {
  position: relative;
  margin-top: 85px;
  @include media-breakpoint-down(xl) {
    margin-top: 70px;
  }
  @include media-breakpoint-down(md) {
    margin-top: 50px;
  }
  &:after {
    content: "";
    background-color: $primary;
    position: absolute;
    left: 0;
    right: 0;
    top: 42px;
    bottom: 42px;
    @include media-breakpoint-down(lg) {
      top: 25px;
      bottom: 25px;
    }
  }
}

.blog-listing-section {
  margin-top: 60px;
  margin-bottom: 60px;
  @include media-breakpoint-down(md) {
    margin-top: 30px;
    margin-bottom: 30px;
  }
}
.blog-banner-wrap {
  display: flex;
  @include media-breakpoint-down(md) {
    flex-direction: column;
  }
  .b-img-wrap {
    flex: 0 0 50%;
    max-width: 50%;
    padding: 8px;
    box-shadow: 0 0 10px rgba(0,0,0,0.16);
    background-color: #fff;
    position: relative;
    z-index: 1;
    @include media-breakpoint-down(md) {
      flex: 0 0 100%;
      max-width: 100%;
    }
    img {
      width: 100%;
    }
  }
  .b-text-wrap {
    flex: 0 0 50%;
    max-width: 50%;
    color: #fff;
    padding-left: 65px;
    position: relative;
    z-index: 1;
    padding-top: 42px;
    padding-bottom: 42px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    a {
      color: #fff;
      text-decoration: none;
    }
    @include media-breakpoint-down(xl) {
      padding-left: 40px;
    }
    @include media-breakpoint-down(md) {
      flex: 0 0 100%;
      max-width: 100%;
      padding-left: 0;
      padding-top: 23px;
    }
    .b-title {
      font-size: 50px;
      font-weight: bold;
      margin-bottom: 25px;
      @include media-breakpoint-down(xl) {
        font-size: 35px;
        margin-bottom: 15px;
      }
      @include media-breakpoint-down(lg) {
        font-size: 26px;
        margin-bottom: 12px;
      }
      
    }
    .b-sub-title {
      font-size: 25px;
      font-weight: 600;
      margin-bottom: 14px;
      @include media-breakpoint-down(xl) {
        font-size: 20px;
        margin-bottom: 10px;
      }
    }
    .b-desc {
      font-size: 18px;
      font-weight: 600;
      line-height: 1.25;
      margin-bottom: 0;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 9;
      -webkit-box-orient: vertical;
      max-height: 198px;
      @include media-breakpoint-down(xxl) {
        -webkit-line-clamp: 6;
        max-height: 132px;
      }
      @include media-breakpoint-down(xl) {
        font-size: 17px;
      }
      @include media-breakpoint-down(lg) {
        -webkit-line-clamp: 6;
        max-height: 84px;
      }
      @include media-breakpoint-down(md) {
        -webkit-line-clamp: 8;
        max-height: 168px;
      }
    }

  }
}
.blog-item-wrap {
  
  .blog-item {
    &:hover {
      .more-btn {
        .ico  {
          margin-left: 5px;
        }
      }
    }
    .b_blog-img-wrap {
      margin-bottom: 28px;
      height: 325px;
      @include media-breakpoint-down(xxl) {
        height: 275px;
      }
      @include media-breakpoint-down(xl) {
        height: 203px;
      }
      @include media-breakpoint-down(lg) {
        height: 230px;
      }
      @include media-breakpoint-down(md) {
        height: inherit;
      }
      .b-img-wrap {
        display: flex;
        align-items: center;
        height: 100%;
        img {
          width: 100%;
        }
      }
    }
    
    .b-body-info {
      .b-title {
        font-size: 24px;
        font-weight: bold;
        margin-bottom: 15px;
        @extend .text-truncate;
        a {
          color: $dark;
          text-decoration: none;
        }
        @include media-breakpoint-down(md) {
          font-size: 22px;
        }
      }
      .b-desc {
        font-family: $secondary-font-family;
        font-size: 18px;
        font-weight: 400;
        line-height: 1.45;
        margin-bottom: 0;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
        height: 78px;
        @include media-breakpoint-down(xxl) {
          -webkit-line-clamp: 4;
          height: 104px;
        }
        @include media-breakpoint-down(xl) {
          -webkit-line-clamp: 6;
          height: 156px;
        }
        @include media-breakpoint-down(lg) {
          -webkit-line-clamp: 5;
          height: 130px;
        }
        @include media-breakpoint-down(md) {
          height: auto;
        }
      }
      .btn {
        margin-top: 10px;
        margin-left: 0px;
        transition: 0.2s ease-out;
      }
    }
  }
  &.landing-visit-blog-item {
    
    .blog-item {
      @include media-breakpoint-down(lg) {
        margin-bottom: 30px;
      }
      .b_blog-img-wrap {
        height: 320px;
        @include media-breakpoint-down(xxl) {
          height: 328px;
        }
        @include media-breakpoint-down(xl) {
          height: 241px;
        }
        @include media-breakpoint-down(lg) {
          height: 274px;
        }
        @include media-breakpoint-down(md) {
          height: auto;
        }
      }
      .b-body-info {
        .b-title {
          font-size: 20px;
        }
      }
    }
  }
}
.single-blog-content {
  .row {
    &:not(:last-child) {
      margin-bottom: 32px;
      @include media-breakpoint-down(lg) {
        margin-bottom: 50px;
      }
      @include media-breakpoint-down(lg) {
        margin-bottom: 30px;
      }
    }
    .img-col {
      @include media-breakpoint-down(lg) {
        order: 2;
        img {
          margin-top: 30px;
          margin-bottom: 15px;
        }
        
      }
      @include media-breakpoint-down(md) {
        img {
          margin-top: 20px;
          margin-bottom: 10px;
        }
      }
    }
    .text-col {
      @include media-breakpoint-down(lg) {
        order: 1;
      }
    }
  }
}